<template>
  <div>
    <h4><i18n :strData="['Atividades e Conhecimentos', 'Activities and Knowlegments']"/></h4>
    <div class="activities m-1 ml-3 mr-3">
      <ul class="nav">
        <li v-for="(item, index) in knowledgments" class="badge badge-info m-1">{{ item }}</li>
      </ul>
      <br />
      <div v-for="(item, index) in knowhow"><i class="fa fa-cogs ml-3 mt-1 mr-2" aria-hidden="true"></i><span v-html="item"></span></div>
    </div>
  </div>
</template>

<script>
import i18n from './i18n'
import { i18nEmitter } from './../assets/js/i18nEmitter.js'

export default {
  components: {
    'i18n': i18n
  },
  name: 'Activities',
  mounted () {
    this.knowhow = (i18nEmitter.currentLang === 'br') ? this.knowhowData.br : this.knowhowData.en
    i18nEmitter.$on('langChange', newLang => {
      this.knowhow = (newLang === 'br') ? this.knowhowData.br : this.knowhowData.en
    })
  },
  data: function () {
    return {
      knowledgments: [
      // Pascal
        'Delphi 7',
        'Lazarus/Pascal',
        'Delphi ASM',
      // .NET
        'Visual Studio 2008',
        'Visual Studio 2010',
        'Visual Studio 2013',
        'Visual Studio 2015',
      // Databases
        'MySQL',
        'Interbase',
        'Firebird',
        'Paradox',
        'SQL Server',
        'Orable',
        'Mongo',
        'Postgree',
        'SQLite',
        'DBMS Stored-Procedures',
        'DBMS Triggers',
        'DBMS Functions/Procedures',
        'Firebird UDF',
      // JavaScript
        'JavaScript',
        'jQuery',
        'JSX',
        'NodeJS',
        'VueJS',
        'Angular',
        'ReactJS',
        'JSON',
      // PHP
        'PHP 4',
        'PHP 5',
        'PHP 7',
        'WordPress',
        'Apache',
        'Docker',
        'Vagrant',
        'Symfony',
        'Laravel',
        'Slim',        
      // Java
        'Java',
        'Android Studio',
        'XML',
      // VCS
        'SVN',
        'Git',
        'Github',
        'Gitlab',
      // Others Languages
        'Perl',
        'Lua',
        'C++',
        'Python',
        'GO',
        'Assembler',
        'Arduino',
      // Utilities
        'VNC',
        'Slack',
        'Atom',
        'NotePad++',
        'VS Code',
      // Others
        'Amazon AWS',
        'Neural Network',
        'NEAT',
        'Blender[3D]',
        'Illustrator',
        'AfterEffects'
      ],
      knowhow: [],
      knowhowData: {
        br: [
          'Paralelismo e sincronia em processos críticos',
          'Redução de custo de processamento',
          'Desenvolvimento de projetos web/API',
          'Decodificação de arquivos',
          'Conhecimentos avançados na VCL Delphi',
          'Conhecimentos avançados nas APIs da plataforma Win32',
          'Conhecimentos avançados em gerenciamento de exceções',
          'Projeto de paralelismo (Intel x86) reduz consumo de pilha em procesamentos paralelos (Delphi):<br /><a href="https://code.google.com/archive/p/paralleljobs/">https://code.google.com/archive/p/paralleljobs/</a>',
          'Projeto de demonstração de trabalhos em modelo de Actors/Mutex/Semáforos (Delphi):<br /><a href="https://code.google.com/archive/p/peebles/">https://code.google.com/archive/p/peebles/</a>',
          'Projeto de Processamento de linguagem natural (NLP: Escrito em C#/Modelo Neural específico):<br /><a href="http://projects.pro.br/nlp/">http://projects.pro.br/nlp/</a>',
          'Projeto que disponibilidade integração de sistema(Internet necessária) com comandos de voz via aparelhos Android(Ecosistema Java e C#):<br /><a href="http://projects.pro.br/opvoice/">http://projects.pro.br/opvoice/</a>',
          'Projeto de assistente de voz para aparelhos Android(Ecosistema Java, Delphi, WordPress, MySQL e NodeJS):<br /><a href="http://projects.pro.br/operator/">http://projects.pro.br/operator/</a>',
          'Projeto de IA (Rede neural) que demonstra uma NN jogando o Space Invader (Javascript):<br /><a href="https://saraivagilberto.github.io/nnspaceinvaders/">https://saraivagilberto.github.io/nnspaceinvaders/</a>'
        ],
        en: [
          'Parallelism and synchronism on critical process',
          'Process cost reduction',
          'Development of web/API projects',
          'File decoding',
          'Advanced knowledgement on VCL Delphi',
          'Advanced knowledgement on Win32 plataform APIs',
          'Advanced knowledgement with exception management',
          'Parallelism projects (Intel x86) reduce stack consuption on parallel process (Delphi):<br /><a href="https://code.google.com/archive/p/paralleljobs/">https://code.google.com/archive/p/paralleljobs/</a>',
          'Demonstration project of Actors/Mutex/Semaphores working model (Delphi):<br /><a href="https://code.google.com/archive/p/peebles/">https://code.google.com/archive/p/peebles/</a>',
          'Natural Processing Language Project (NLP: Written with C#/Specific Neural Model):<br /><a href="http://projects.pro.br/nlp/">http://projects.pro.br/nlp/</a>',
          'Project that enable system integration(Internet required) with voice commands through Android devices (Ecosystem Java and C#):<br /><a href="http://projects.pro.br/opvoice/">http://projects.pro.br/opvoice/</a>',
          'Voice Assistant project for Android devices(Ecosystem Java, Delphi, WordPress, MySQL and NodeJS):<br /><a href="http://projects.pro.br/operator/">http://projects.pro.br/operator/</a>',
          'AI Project (Neural Network) that demonstrate a NN playing the Space Invader (Javascript):<br /><a href="https://saraivagilberto.github.io/nnspaceinvaders/">https://saraivagilberto.github.io/nnspaceinvaders/</a>'
        ]
      }
    }
  }
}
</script>



