          <i>Desenvolvimento/Análise de Softwares e Hardwares (Técnico)</i><br />
          <sup>Colégio Brasileiro - (2001-2002)</sup>
        </li>
      </ul>
    </div>

    <h4><i18n :strData="['Idiomas Extrangeiros', 'Foreign Languages']"/></h4>
