      <ul>
        <li><i18n :strData="['Inglês Avançado (Boa base em terminologia para desenvolvimento de softwares)', 'Brazilian Portuguese']"/></li>
        <li><i18n :strData="['Espanhol Intermediário', 'Intermediate Spanish']"/></li>
      </ul>
    </div>
  </div>
</template>

<script>
import i18n from './i18n'
export default {
  components: {
    'i18n': i18n
